<template>
<section class="hero">
  <div class="hero-body">
    <div class="my-content">
      <div class="container has-text-centered">
        <div class="content-title">
          <h1 class="title" v-html="title" />
          <h2 class="subtitle" v-html="subtitle" />
        </div>
      </div>
      <div class="container">
        <div class="columns">
          <div class="column" v-for="(item, idx) in youtube" :key="idx">
            <div class="videoWrapper">
              <iframe v-if="item.src"
                width="560"
                height="315"
                :src="item.src"
                frameborder="0"
                allow="autoplay; encrypted-media"
                allowfullscreen />
              <p v-else class="has-text-centered">
                <i class="fa fa-4x fa-exclamation-triangle has-text-warning"
                  aria-hidden="true">
                </i><br>
                To be updated
              </p>
            </div>
            <p class="is-title is-4 has-text-centered">{{item.title}}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</section>
</template>

<script>
export default {
  data() {
    return {
      title: 'Multi-Lingual Speech Synthesis',
      subtitle: 'English <i class="fa fa-arrows-h"></i> Korean',
      youtube: [
        { src: 'https://www.youtube.com/embed/kK0WHyOrHbw', title: 'Donald Trump speaks Korean to meet North Korean Leader' },
        // { src: 'https://www.youtube.com/embed/0i7baNlXiFM', title: 'Kim Jung Un speaks English to meet Donald Trump' },
      ],
    };
  },
};
</script>


<style lang="scss" scoped>
@import 'src/assets/sass/_variables';

.container {
  @media screen and (min-width: $desktop) {
    padding-left: 5em;
    padding-right: 5em;
  }
  @media screen and (min-width: $fullhd) {
    padding-left: 10em;
    padding-right: 10em;
  }
}

.my-content {
  padding-top: 3em;
  padding-bottom: 3em;
}

.content-title {
  padding-bottom: 3em;
}

.columns .column {
  @media screen and (min-width: $tablet), print {
    padding-left: 20%;
    padding-right: 20%;
  }
}

.videoWrapper {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 */
  padding-top: 25px;
  height: 0;
}
.videoWrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
</style>



