<template>
  <div id="app">
    <navigation />
    <router-view/>
    <foot />

    <vue-progress-bar />
    <notifications
      group="error"
      position="top center"
      classes="is-danger"
      />
    <notifications
      group="success"
      position="top center"
      classes="is-success"
      />
  </div>
</template>

<script>
import Navigation from '@/components/Navigation';
import Foot from '@/components/Footer';

export default {
  components: {
    Navigation,
    Foot,
  },

  name: 'App',
};
</script>

<style>
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* text-align: center; */
}
</style>



