<template>
  <div>
    <video-samples class="is-dark"/>
    <switch-language class="is-link" />
    <!-- <abstract class="is-dark"/> -->
    <audio-samples class="is-dark" />
  </div>
</template>

<script>
import VideoSamples from './VideoSamples';
import Abstract from './Abstract';
import AudioSamples from './AudioSamples';
import SwitchLanguage from './SwitchLanguage';

export default {
  components: {
    VideoSamples,
    Abstract,
    AudioSamples,
    SwitchLanguage,
  },

  mounted() {
    if (this.$route.hash) {
      this.$scrollTo(this.$route.hash);
    }
  },
};
</script>

<style lang="scss" scoped>
</style>



