<template>
<section class="hero">
  <div class="hero-body">
    <div class="my-content">
      <div class="container has-text-centered">
        <div class="content-title">
          <h1 class="title" v-html="title" />
          <h2 class="subtitle" v-html="subtitle" />
        </div>
      </div>
      <div class="container">
        <div class="columns">
          <div class="column">
            <div class="videoWrapper">
              <iframe
                width="560"
                height="315"
                :src="youtube"
                frameborder="0"
                allow="autoplay; encrypted-media"
                allowfullscreen />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</section>
</template>

<script>
export default {
  data() {
    return {
      title: 'Language switching',
      subtitle: 'Trump speech at Korean National Assembly',
      youtube: 'https://www.youtube.com/embed/yJYYNOjmydI',
    };
  },
};
</script>

<style lang="scss" scoped>
@import 'src/assets/sass/_variables';

.container {
  @media screen and (min-width: $desktop) {
    padding-left: 5em;
    padding-right: 5em;
  }
  @media screen and (min-width: $fullhd) {
    padding-left: 10em;
    padding-right: 10em;
  }
}

.my-content {
  padding-top: 2em;
  padding-bottom: 2em;
}

.content-title {
  padding-bottom: 3em;
}

.columns .column {
  @media screen and (min-width: $tablet), print {
    padding-left: 20%;
    padding-right: 20%;
  }
}

.videoWrapper {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 */
  padding-top: 25px;
  height: 0;
}
.videoWrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

</style>



