<template>
<div class="container content">
  <hr>
  <div class="columns has-text-centered is-mobile is-vcentered">
    <div class="column is-two-thirds">Text</div>
    <div class="column"><p>Native English</p></div>
    <div class="column"><p>Native Korean</p></div>
  </div>
  <div class="columns has-text-centered is-mobile is-vcentered"
      v-for="(item, idx) in sentences" :key="idx">
    <div class="column is-two-thirds">
      <p>{{item.sentence}}</p>
    </div>
    <div class="column">
      <audio-player :sources="[`${item.urlpre}_cmu_slt.wav`]" :formats="['wav']" />
    </div>
    <div class="column">
      <audio-player :sources="[`${item.urlpre}_korean_fv20.wav`]" :formats="['wav']" />
    </div>
  </div>
</div>
</template>

<script>
import AudioPlayer from '../common/AudioPlayer';

export default {
  components: {
    AudioPlayer,
  },

  props: [
    'sentences',
  ],

  mounted() {
  },
};
</script>


<style lang="scss" scoped>
.column {
  padding-left: 0;
  padding-right: 0;
}
</style>



