<template>
<section class="hero is-fullheight is-bold">
  <div class="hero-body has-text-centered">
    <div class="container">
      <h1 class="title" v-html="title" />
      <h2 class="subtitle" v-html="subtitle" />
    </div>
    <vue-particles
        class="my-particles"
        color="#fff"
        shapeType="polygon"
        :hoverEffect="false"
        :lineOpacity="0.6"
        :particlesNumber="30"
        :moveSpeed="5"
        :linesDistance="140"
        />
  </div>
  <a href="#" v-scroll-to="'#product'" class="down has-text-centered has-text-primary">
    <i class="fa fa-chevron-down" aria-hidden="true"></i>
  </a>
</section>
</template>

<script>
import VueParticles from '../common/vue-particles/vue-particles';

export default {
  components: {
    VueParticles,
  },

  data() {
    return {
      title: 'Icepick<sup class="has-text-primary">AI</sup>',
      subtitle: 'Speak any language in your video',
    };
  },
};
</script>

<style lang="scss" scoped>
@import 'src/assets/sass/_variables';

.ie-height {
  height: 100vh;
  display: table-cell;
  vertical-align: middle;
}

.title {
  font-size: 4em;
  @media screen and (min-width: $tablet - 100px), print {
    font-size: 6.5em;
  }
}

.subtitle {
  font-size: 0.95em;
  @media screen and (min-width: $tablet - 100px), print {
    font-size: 1.7em;
  }
}

.down {
  position:absolute;
  bottom: 2em;
  width:100%;
  margin:0 auto;
  display:block;
  font-size:30px;
  animation: slide1 1s ease-in-out infinite;

  :hover {
    opacity: 0.2;
  }
}

.my-particles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@keyframes slide1 {
  0%,
  100% {
    transform: translate(0, 0);
  }

  50% {
    transform: translate(0, 0.5em);
    opacity: 0.3;
  }
}
</style>



