import Vue from 'vue';
import Router from 'vue-router';
import HomePage from '@/components/Home/Index';
import DemoPage from '@/components/Demo/Index';

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'HomePage',
      component: HomePage,
    },
    {
      path: '/demo',
      name: 'DemoPage',
      component: DemoPage,
    },
  ],

  scrollBehavior() {
    return { x: 0, y: 0 };
  },
});



