<template>
<div>
  <div class="columns is-vcentered is-mobile" v-if="idx % 2 === 1">
    <div class="column is-one-third">
        <span class="icon-img"><i class="fa" :class="img" ></i></span>
    </div>
    <div class="column">
      <h3 class="title is-4" v-html="title" />
      <p v-html="desc" />
    </div>
  </div>
  <div class="columns is-vcentered is-mobile" v-else>
    <div class="column">
      <h3 class="title is-4" v-html="title" />
      <p v-html="desc" />
    </div>
    <div class="column is-one-third">
        <span class="icon-img"><i class="fa" :class="img" ></i></span>
    </div>
  </div>
</div>
</template>

<script>
export default {
  props: [
    'img', 'title', 'desc', 'idx',
  ],
};
</script>

<style lang="scss" scoped>
@import 'src/assets/sass/_variables';

.icon-img {
  font-size: 10em;
  color: $grey-dark;

  @media screen and (max-width: $tablet - 1px) {
    font-size: 8em;
    color: $grey-dark;
  }
}
</style>



