<template>
  <div class="content">
    <button v-if="playing" @click="stop" class="button is-danger is-rounded">
      <i class="fa fa-stop"></i>
    </button>
    <button v-else @click="play" class="button is-success is-rounded">
      <i class="fa fa-play"></i>
    </button>
  </div>
</template>

<script>
import VueHowler from 'vue-howler';

export default {
  mixins: [VueHowler],
  computed: {
    validProgress() {
      return ((this.progress || 0) * 100);
    },
  },
};
</script>

<style lang="scss" scoped>
button {
  margin: 0;
}
</style>



