<template>
  <section class="hero is-medium has-text-centered">
    <div class="hero-body">
      <div class="container">
        <h1 class="title more-margin" v-html="title" />
        <feature v-for="(item, idx) in features"
          :key="idx"
          :title="item.title"
          :img="item.img"
          :desc="item.desc"
          :idx="idx"
          />
      </div>
    </div>
  </section>
</template>

<script>
import Feature from './Feature';

export default {
  components: {
    Feature,
  },

  data() {
    return {
      title: 'Multi-language video nearly identical experience to the original',
      features: [
        {
          img: 'fa-globe',
          title: 'Consistent',
          desc: 'Mimic speaker identity, prosody, and emotion of the original language',
        },
        {
          img: 'fa-cog',
          title: 'Customizable',
          desc: 'Adjust fluency, accent, and dialect of the target language',
        },
        {
          img: 'fa-child',
          title: 'Easy to use',
          desc: 'Put your video and translation, and get the target language video',
        },
      ],
    };
  },
};
</script>

<style lang="scss" scoped>
@import 'src/assets/sass/_variables';

.more-margin {
  margin-bottom: 5em;
}

.container {
  @media screen and (min-width: $tablet - 100px), print {
    padding-left: 13%;
    padding-right: 13%;
  }

  @media screen and (min-width: $desktop) {
    padding-left: 17%;
    padding-right: 17%;
  }

  @media screen and (min-width: $fullhd) {
    padding-left: 200px;
    padding-right: 200px;
  }
}
</style>



