<template>
<nav class="navbar is-fixed-top has-text-left layout-center"
    :class="{'is-transparent': isTransparent}">
  <div class="navbar-brand">
    <a class="navbar-item" href="/">
      <img src="/static/img/logo.png" />
    </a>

    <div class="navbar-burger burger has-text-light" data-target="navbarExampleTransparentExample">
      <span></span>
      <span></span>
      <span></span>
    </div>
  </div>

  <div id="navbarExampleTransparentExample" class="navbar-menu">
    <div class="navbar-start">
      <router-link to="/" class="navbar-item" >
        Home
      </router-link>
      <router-link to="/demo" class="navbar-item" >
        Demo
      </router-link>
    </div>
  </div>
</nav>
</template>

<script>
export default {
  data() {
    return {
      scrolled: false,
      openedMenu: false,
    };
  },

  mounted() {
    const $navbarBurgers = Array.prototype.slice.call(document.querySelectorAll('.navbar-burger'), 0);
    if ($navbarBurgers.length > 0) {
      $navbarBurgers.forEach(($el) => {
        $el.addEventListener('click', () => {
          const target = $el.dataset.target;
          const $target = document.getElementById(target);
          $el.classList.toggle('is-active');
          $target.classList.toggle('is-active');
          this.updateMenuState();
        });
      });
    }

    const $navItems = document.querySelectorAll('.navbar-item');
    $navItems.forEach(($el) => {
      $el.addEventListener('click', () => {
        this.inactiveBurger();
      });
    });

    this.scrolled = window.pageYOffset > 0;
  },

  methods: {
    handleScroll() {
      this.scrolled = window.pageYOffset > 0;
      this.inactiveBurger();
    },

    inactiveBurger() {
      const $navbarBurgers = Array.prototype.slice.call(document.querySelectorAll('.navbar-burger'), 0);
      if ($navbarBurgers.length > 0) {
        $navbarBurgers.forEach(($el) => {
          const target = $el.dataset.target;
          const $target = document.getElementById(target);
          $el.classList.remove('is-active');
          $target.classList.remove('is-active');
        });
      }
      this.updateMenuState();
    },

    updateMenuState() {
      const $navMenu = document.querySelector('.navbar-menu');
      this.openedMenu = $navMenu.classList.contains('is-active');
    },
  },

  computed: {
    isTransparent() {
      return !(this.scrolled || this.openedMenu);
    },
  },

  created() {
    window.addEventListener('scroll', this.handleScroll);
  },

  destroyed() {
    window.removeEventListener('scroll', this.handleScroll);
  },
};
</script>

<style lang="scss" scoped>
@import 'src/assets/sass/_variables';

.layout-center {
  @media screen and (min-width: $desktop) {
    padding-left: 15%;
    padding-right: 15%;
  }
}
</style>



