<template>
  <section class="hero has-text-centered">
    <div class="hero-body">
      <div class="container">
        <h1 class="title" v-html="title" />
        <p v-html="subtitle" />

        <form class="form" @submit.prevent="validateBeforeSubmit">
          <div class="field">
            <label class="label has-text-left is-small">FULL NAME
              <sup class="has-text-danger">*</sup>
            </label>
            <div class="control has-icons-left has-icons-right">
              <input class="input is-rounded"
                  type="text"
                  placeholder="Name"
                  name="fullname"
                  v-model="form.fullname"
                  v-validate="'required|min:2|max:50'"
                  :class="{'is-danger': errors.has('fullname') }"
                  >
              <span class="icon is-small is-left">
                <i class="fa fa-user"></i>
              </span>
              <span class="icon is-small is-right">
                <i class="fa fa-check" :class="{
                    'has-text-success': !errors.has('fullname') && form.fullname !== ''
                    }"></i>
              </span>
            </div>
            <p v-show="errors.has('fullname')" class="help is-danger">
              {{ errors.first('fullname') }}
            </p>
          </div>

          <div class="field">
            <label class="label has-text-left is-small">ORGANIZATION NAME
              <sup class="has-text-danger">*</sup>
            </label>
            <div class="control has-icons-left has-icons-right">
              <input class="input is-rounded"
                  type="text"
                  placeholder="Organization"
                  name="organization"
                  v-model="form.organization"
                  v-validate="'required|max:50'"
                  :class="{'is-danger': errors.has('organization') }"
                  >
              <span class="icon is-small is-left">
                <i class="fa fa-group"></i>
              </span>
              <span class="icon is-small is-right">
                <i class="fa fa-check" :class="{
                    'has-text-success': !errors.has('organization') && form.organization !== ''
                    }"></i>
              </span>
            </div>
            <p v-show="errors.has('organization')" class="help is-danger">
              {{ errors.first('organization') }}
            </p>
          </div>

          <div class="field">
            <label class="label has-text-left is-small">BUSINESS EMAIL
              <sup class="has-text-danger">*</sup>
            </label>
            <div class="control has-icons-left has-icons-right">
              <input class="input is-rounded"
                  type="text"
                  placeholder="Email"
                  name="email"
                  v-model="form.email"
                  v-validate="'required|email|max:80'"
                  :class="{'is-danger': errors.has('email') }"
                  >
              <span class="icon is-small is-left">
                <i class="fa fa-envelope"></i>
              </span>
              <span class="icon is-small is-right">
                <i class="fa fa-check" :class="{
                    'has-text-success': !errors.has('email') && form.email !== ''
                    }"></i>
              </span>
            </div>
            <p v-show="errors.has('email')" class="help is-danger">
              {{ errors.first('email') }}
            </p>
          </div>

          <div class="field">
            <label class="label has-text-left is-small">
              TELL US ABOUT YOUR SPECIFIC PROJECT OR INTEREST.
              <sup class="has-text-danger">*</sup>
            </label>
            <div class="control">
              <textarea class="textarea"
                  placeholder="Tell us"
                  rows="3"
                  name="memo"
                  v-model="form.memo"
                  v-validate="'required|max:100'"
                  :class="{'is-danger': errors.has('memo') }"
                  />
            </div>
            <p v-show="errors.has('memo')" class="help is-danger">
              {{ errors.first('memo') }}
            </p>
          </div>

          <div class="field">
            <div class="control">
              <button v-if="!sentSuccess"
                  class="button is-rounded is-primary"
                  :class="{'is-loading': sending}">
                Request
              </button>
              <button v-else class="button is-rounded is-success" disabled>
                Thank you!
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </section>
</template>

<script>
// import axios from 'axios';
import jsonp from 'jsonp';

// const queryString = require('query-string');

export default {
  data() {
    return {
      title: 'Request Early Access',
      subtitle: 'Interested in our product?<br>Email us at <strong class="has-text-success">ask@icepick.ai</strong> or fill out the form below to be updated.',
      submitUrl: 'https://getsimpleform.com/messages/ajax',
      sentSuccess: false,
      sending: false,
      form: {
        fullname: '',
        email: '',
        organization: '',
        memo: '',
        form_api_token: 'eb446bb82bc16c4a32ad54627b29ffd2',
      },
    };
  },

  methods: {
    validateBeforeSubmit() {
      this.$validator.validateAll().then((result) => {
        if (result) {
          this.onSubmit();
          return;
        }
        this.$notify({
          group: 'error',
          title: 'Error',
          text: 'Please fill out the form',
        });
      });
    },

    onSubmit() {
      this.$Progress.start();
      this.sending = true;
      const q = this.objectToQuerystring(this.form);
      jsonp(`${this.submitUrl}${q}`, (err) => {
        if (err) {
          this.sending = false;
          this.$Progress.fail();
          this.$notify({
            group: 'error',
            title: 'Error',
            text: 'Something wrong with us.',
          });
        } else {
          this.sending = false;
          this.sentSuccess = true;
          this.$Progress.finish();
          this.$notify({
            group: 'success',
            title: 'Success',
            text: 'Thank you!',
          });
        }
      });
    },

    objectToQuerystring(obj) {
      return Object.keys(obj).reduce((str, key, i) => {
        const delimiter = (i === 0) ? '?' : '&';
        const newKey = encodeURIComponent(key);
        const val = encodeURIComponent(obj[key]);
        return [str, delimiter, newKey, '=', val].join('');
      }, '');
    },
  },
};
</script>


<style lang="scss" scoped>
@import 'src/assets/sass/_variables';

.container {
  @media screen and (min-width: $tablet - 100px), print {
    padding-left: 20%;
    padding-right: 20%;
  }

  @media screen and (min-width: $desktop) {
    padding-left: 25%;
    padding-right: 25%;
  }

  @media screen and (min-width: $fullhd) {
    padding-left: 300px;
    padding-right: 300px;
  }

  padding-left: 1em;
  padding-right: 1em;
}

form {
  padding-top: 2em;
}

.textarea {
    border-radius: 1em;
}

.field {
  .label {
    padding-left: 1em;
  }
  .control .button {
    margin-top:1em;
  }
}

</style>




