<template>
  <div>
    <intro id="intro" class="is-dark" />
    <product id="product" class="is-white is-bold" />
    <join-us id="join-us" class="is-dark is-bold" />
  </div>
</template>

<script>
import Intro from './Intro';
import Product from './Product';
import JoinUs from './JoinUs';

export default {
  components: {
    Intro,
    Product,
    JoinUs,
  },

  mounted() {
    if (this.$route.hash) {
      this.$scrollTo(this.$route.hash);
    }
  },
};
</script>



